import React from 'react'
import './nav.css'
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import LocalMallIcon from '@mui/icons-material/LocalMall';
import SearchIcon from '@mui/icons-material/Search';

export default function Nav() {

    const myFunction = (e) => {
        e.preventDefault()
        var x = document.getElementById("myTopnav");
        if (x.className === "topnav") {
            x.className += " responsive";
        } else {
            x.className = "topnav";
        }
    }
    return (
        <>
            <div className="topnav" id="myTopnav">
                <div href="#home"  className="active">
                    TOPZFOOD
                </div>

                <div className="push">
                    <a id='a'></a>
                    <a id='a'>HOME</a>
                    <a id='a'>FEATURES</a>
                    <a id='a'>SHOP</a>
                    <a id='a'>COLLECTION</a>
                    <a id='a'>PRODUCTS</a>
                    <a id='a'>CONTACT US</a>

                </div>

                <div class="box">
                    <form name="search">
                        <input type="text" className="input" name="txt" onmouseout="this.value = ''; this.blur();" />
                        <SearchIcon id='searchIcon' />
                    </form>


                </div><div id='seperator'>|</div>
                <LocalMallIcon id='BagIcon' />
               
                <a id='aicon' className="icon" onClick={myFunction}>
                    <input type="text" className='TopSearch' />
                    <SearchIcon id='TopSrcIcon' />
                    <ArrowDropDownIcon id='TopSrcIcon2' />
                </a>


            </div>
        </>
    )
}
